:root {
  --bg-color: #1e1e1e;
  --tab-bg: #252525;
  --tab-active: #303030;
  --text-color: #e0e0e0;
  --accent-color: #3a3a3a;
  --button-hover: #404040;
  --button-active: #505050;
  --heart-color: #f55;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
}

.soundboard-container {
  grid-template: "controls controls"
                 "tabs tabs"
                 "sounds sidebar" 1fr
                 / 1fr 220px;
  height: 100vh;
  display: grid;
  overflow: hidden;
}

.controls-container {
  background-color: var(--accent-color);
  border-bottom: 1px solid #444;
  grid-area: controls;
  align-items: center;
  padding: 10px;
  display: flex;
}

.stop-button {
  color: var(--text-color);
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  padding: 8px 16px;
  font-weight: bold;
  display: flex;
}

.stop-button.active {
  background-color: #c33;
}

.stop-button:hover {
  background-color: var(--button-hover);
}

.volume-control {
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.volume-control span {
  white-space: nowrap;
  margin: 0 10px;
}

.volume-slider {
  appearance: none;
  background: #555;
  border-radius: 2px;
  outline: none;
  width: 200px;
  height: 4px;
}

.volume-slider::-webkit-slider-thumb {
  appearance: none;
  background: var(--text-color);
  cursor: pointer;
  border-radius: 50%;
  width: 16px;
  height: 16px;
}

.tabs-container {
  background-color: var(--tab-bg);
  border-bottom: 1px solid #444;
  grid-area: tabs;
  padding: 5px 5px 0;
  display: flex;
}

.tab {
  background-color: var(--tab-bg);
  color: var(--text-color);
  cursor: pointer;
  border: none;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  margin-right: 2px;
  padding: 8px 16px;
  font-size: 12px;
  font-weight: bold;
}

.tab.active {
  background-color: var(--tab-active);
  border-bottom: 2px solid #666;
}

.tab:hover:not(.active) {
  background-color: var(--button-hover);
}

.sounds-container {
  flex-direction: column;
  grid-area: sounds;
  padding: 10px;
  display: flex;
  overflow-y: auto;
}

.sound-item {
  cursor: pointer;
  border-bottom: 1px solid #333;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.sound-item:hover {
  background-color: var(--button-hover);
}

.sound-name {
  flex-grow: 1;
}

.sound-actions {
  gap: 5px;
  display: flex;
}

.favorite-button, .edit-button {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: none;
  padding: 5px;
}

.favorite-button.active {
  color: var(--heart-color);
}

.favorite-button:hover, .edit-button:hover {
  color: #fff;
}

.sidebar-container {
  background-color: var(--accent-color);
  border-left: 1px solid #444;
  flex-direction: column;
  grid-area: sidebar;
  padding: 10px;
  display: flex;
}

.sidebar-button {
  color: var(--text-color);
  cursor: pointer;
  text-align: left;
  background: none;
  border: none;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 5px;
  padding: 10px;
  font-weight: bold;
  display: flex;
}

.sidebar-button svg {
  margin-right: 10px;
}

.sidebar-button:hover {
  background-color: var(--button-hover);
}

.sidebar-button.active {
  background-color: var(--button-active);
}

.room-info {
  color: #666;
  font-size: 12px;
  position: fixed;
  bottom: 10px;
  left: 10px;
}

.home-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
  display: flex;
}

.home-container h1 {
  margin-bottom: 30px;
}

.room-actions {
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 400px;
  display: flex;
}

.create-room-button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 12px 24px;
  font-size: 16px;
}

.join-room-form {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.join-room-form input {
  background-color: var(--accent-color);
  color: var(--text-color);
  border: 1px solid #444;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

.join-room-form button {
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  border-radius: 4px;
  padding: 12px 24px;
  font-size: 16px;
}
